import React, { useEffect, useState } from 'react';
import { ref, get, remove, set } from 'firebase/database';
import { database } from '../Firebase';
import { useNavigate } from 'react-router-dom';

export default function Language() {
  const [languages, setLanguages] = useState([]);
  const [newLang, setNewLang] = useState('');
  const navigate = useNavigate();

  //  Login check
  useEffect(() => {
    const user = sessionStorage.getItem('user');
    if (!user) navigate('/');
  }, [navigate]);

  //  Fetch language list on load
  useEffect(() => {
    const fetchLanguages = async () => {
      const langRef = ref(database, 'qna/languages');
      const snapshot = await get(langRef);
      const langs = snapshot.val() || [];
      setLanguages(langs);
    };
    fetchLanguages();
  }, []);

  //  Add new language
  const addLanguage = async () => {
    if (!newLang.trim()) return;
    const updated = [...languages, newLang.toLowerCase()];
    await set(ref(database, 'qna/languages'), updated);
    setLanguages(updated);
    setNewLang('');
  };

  //  Delete language
  const deleteLanguage = async (lang) => {
    if (!window.confirm(`Are you sure you want to delete "${lang}"?`)) return;

    await remove(ref(database, `qna/${lang}`)); // Delete QnA node
    const updated = languages.filter((l) => l !== lang); // Remove from list
    await set(ref(database, 'qna/languages'), updated);
    setLanguages(updated);
  };

  //  Logout
  const handleLogout = () => {
    sessionStorage.removeItem('user');
    navigate('/');
  };

  return (
    <div
      style={{
        display: 'flex',
        height: '100vh',
        fontFamily: 'Segoe UI, sans-serif',
        backgroundColor: '#f1f3f5',
      }}
    >
      {/*  Left: Language List */}
      <div
        style={{
          width: '280px',
          background: '#ffffff',
          borderRight: '1px solid #dee2e6',
          padding: '1.5rem 1rem',
        }}
      >
        <h3 style={{ marginBottom: '1rem', color: '#343a40' }}>Languages</h3>
        {languages.length === 0 && <p>No languages found.</p>}
        {languages.map((lang) => (
          <div
            key={lang}
            style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              padding: '0.5rem 0.75rem',
              backgroundColor: '#f8f9fa',
              borderRadius: 5,
              marginBottom: '0.5rem',
              border: '1px solid #dee2e6',
            }}
          >
            <span style={{ fontWeight: 500 }}>{lang.toUpperCase()}</span>
            <button
              onClick={() => deleteLanguage(lang)}
              style={{
                backgroundColor: '#dc3545',
                color: '#fff',
                border: 'none',
                padding: '0.25rem 0.5rem',
                borderRadius: 4,
                cursor: 'pointer',
                fontWeight: 'bold',
              }}
            >
              
            </button>
          </div>
        ))}
      </div>

      {/*  Right: Add Language + Logout */}
      <div
        style={{
          flex: 1,
          padding: '2rem',
          position: 'relative',
        }}
      >
        {/*  Logout Button */}
        <button
          onClick={handleLogout}
          title='Logout'
          style={{
            position: 'absolute',
            right: 20,
            top: 20,
            backgroundColor: '#e74c3c',
            color: '#fff',
            border: 'none',
            padding: '8px 14px',
            borderRadius: 6,
            cursor: 'pointer',
            fontSize: 14,
            fontWeight: 'bold',
          }}
        >
           Logout
        </button>

        <h2 style={{ color: '#343a40', marginBottom: '1rem' }}>
          Add New Language
        </h2>
        <input
          type='text'
          placeholder='Enter new language name'
          value={newLang}
          onChange={(e) => setNewLang(e.target.value)}
          style={{
            padding: '0.75rem',
            width: '100%',
            maxWidth: '400px',
            borderRadius: 6,
            border: '1px solid #ced4da',
            fontSize: '16px',
            marginBottom: '1rem',
            background: '#fff',
          }}
        />
        <br />
        <button
          onClick={addLanguage}
          style={{
            backgroundColor: '#28a745',
            color: '#fff',
            padding: '0.6rem 1.2rem',
            fontSize: '16px',
            border: 'none',
            borderRadius: 6,
            cursor: 'pointer',
            fontWeight: 600,
          }}
        >
           Add Language
        </button>
      </div>
    </div>
  );
}
