import React, { useState } from 'react';
import axios from 'axios';
import Fuse from 'fuse.js';

export default function Wikipedia() {
  const [query, setQuery] = useState('');
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);
  const [suggestions, setSuggestions] = useState([]);

  // Helper to fetch summary by title
  const fetchSummary = async (title) => {
    try {
      setLoading(true);
      setAnswer('');
      setCopied(false);
      setSuggestions([]);

      const contentRes = await axios.get(`https://en.wikipedia.org/w/api.php`, {
        params: {
          action: 'query',
          prop: 'extracts',
          exintro: false,
          explaintext: true,
          titles: title,
          format: 'json',
          origin: '*',
        },
      });

      const pages = contentRes.data.query.pages;
      const page = pages[Object.keys(pages)[0]];
      let content = page.extract || 'No content found.';
      content = content.replace(/\[\d+\]/g, ''); // remove refs like [1]

      setAnswer(content);
    } catch (error) {
      console.error(error);
      setAnswer('Error fetching from Wikipedia or no result found.');
    } finally {
      setLoading(false);
    }
  };

  const searchWikipedia = async () => {
    if (!query.trim()) return;
    setLoading(true);
    setAnswer('');
    setCopied(false);
    setSuggestions([]);

    try {
      // Step 1: Get search results (multiple matches)
      const searchRes = await axios.get(`https://en.wikipedia.org/w/api.php`, {
        params: {
          action: 'query',
          list: 'search',
          srsearch: query,
          format: 'json',
          origin: '*',
        },
      });

      const searchResults = searchRes.data.query?.search || [];

      if (searchResults.length === 0) {
        setAnswer('No matching topic found on Wikipedia.');
        setLoading(false);
        return;
      }

      // Use Fuse.js to find closest match among titles
      const fuse = new Fuse(searchResults, {
        keys: ['title'],
        threshold: 0.4, // adjust this for fuzziness
      });

      const fuseResult = fuse.search(query);

      let bestMatchTitle = null;

      if (fuseResult.length > 0) {
        bestMatchTitle = fuseResult[0].item.title;
      } else {
        // fallback to first search result
        bestMatchTitle = searchResults[0].title;
      }

      await fetchSummary(bestMatchTitle);

      // Store other suggestions except the best one
      setSuggestions(
        searchResults
          .filter((item) => item.title !== bestMatchTitle)
          .slice(0, 4)
      );
    } catch (error) {
      console.error(error);
      setAnswer('Error fetching from Wikipedia or no result found.');
      setLoading(false);
    }
  };

  const handleCopy = () => {
    if (!answer) return;
    navigator.clipboard.writeText(answer);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleClear = () => {
    setQuery('');
    setAnswer('');
    setCopied(false);
    setSuggestions([]);
  };

  return (
    <div style={{ maxWidth: 600, margin: '40px auto', padding: 20 }}>
      <h2 style={{ textAlign: 'center', color: '#2c3e50' }}>Ask Wikipedia</h2>

      <input
        type='text'
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        placeholder='Ask about a topic...'
        style={{
          width: '100%',
          padding: 10,
          marginBottom: 10,
          fontSize: 16,
          border: '1px solid #ccc',
          borderRadius: 6,
        }}
      />

      <div style={{ display: 'flex', gap: 10 }}>
        <button
          onClick={searchWikipedia}
          disabled={loading}
          style={{
            flex: 1,
            padding: 10,
            backgroundColor: '#2980b9',
            color: '#fff',
            fontWeight: 'bold',
            border: 'none',
            borderRadius: 6,
            cursor: 'pointer',
          }}
        >
          {loading ? 'Searching...' : 'Get Answer'}
        </button>

        <button
          onClick={handleClear}
          disabled={loading && !query && !answer}
          style={{
            padding: 10,
            backgroundColor: '#e74c3c',
            color: '#fff',
            fontWeight: 'bold',
            border: 'none',
            borderRadius: 6,
            cursor: 'pointer',
          }}
        >
          Clear
        </button>
      </div>

      {suggestions.length > 0 && (
        <div
          style={{
            marginTop: 15,
            backgroundColor: '#eef2f7',
            padding: 10,
            borderRadius: 6,
          }}
        >
          <strong>Did you mean:</strong>
          <ul style={{ paddingLeft: 20, marginTop: 10 }}>
            {suggestions.map((s) => (
              <li key={s.pageid}>
                <button
                  onClick={() => fetchSummary(s.title)}
                  style={{
                    background: 'none',
                    border: 'none',
                    color: '#2980b9',
                    cursor: 'pointer',
                    textDecoration: 'underline',
                    fontSize: 16,
                  }}
                >
                  {s.title}
                </button>
              </li>
            ))}
          </ul>
        </div>
      )}

      {answer && (
        <div
          style={{
            marginTop: 20,
            backgroundColor: '#f4f4f4',
            padding: 15,
            borderRadius: 6,
            position: 'relative',
            whiteSpace: 'pre-wrap',
          }}
        >
          <strong>Answer:</strong>
          <p style={{ marginTop: 8 }}>{answer}</p>

          <button
            onClick={handleCopy}
            style={{
              position: 'absolute',
              top: 12,
              right: 12,
              backgroundColor: copied ? '#27ae60' : '#3498db',
              color: '#fff',
              border: 'none',
              borderRadius: 4,
              padding: '6px 10px',
              cursor: 'pointer',
              fontSize: 14,
            }}
          >
            {copied ? 'Copied!' : 'Copy'}
          </button>
        </div>
      )}
    </div>
  );
}
