import React, { useState } from 'react';
import axios from 'axios';

export default function Gemini() {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);

  const API_KEY = 'AIzaSyAYbj5Mt8-REAdlnbWosTGJPXrVNd-9pEg';
  const url = `https://generativelanguage.googleapis.com/v1/models/gemini-1.5-flash:generateContent?key=${API_KEY}`;

  const handleAsk = async () => {
    if (!question.trim()) return;
    setLoading(true);
    setAnswer('');
    setCopied(false);

    const body = {
      contents: [
        {
          parts: [{ text: question }],
        },
      ],
    };

    try {
      const res = await axios.post(url, body);
      const output =
        res.data.candidates?.[0]?.content?.parts?.[0]?.text || 'No answer.';
      setAnswer(output);
    } catch (err) {
      console.error(err);
      setAnswer(
        'Error: ' + (err.response?.data?.error?.message || err.message)
      );
    } finally {
      setLoading(false);
    }
  };

  const handleCopy = () => {
    if (!answer) return;
    navigator.clipboard.writeText(answer);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleClear = () => {
    setQuestion('');
    setAnswer('');
    setCopied(false);
  };

  return (
    <div
      style={{
        maxWidth: 600,
        margin: '40px auto',
        padding: 20,
        border: '1px solid #ddd',
        borderRadius: 8,
        boxShadow: '0 4px 12px rgba(0,0,0,0.05)',
        fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
        backgroundColor: '#f9f9f9',
      }}
    >
      <h2 style={{ textAlign: 'center', color: '#2c3e50' }}>Ask Gemini AI</h2>

      <textarea
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        placeholder='Type your question here...'
        rows={4}
        style={{
          width: '100%',
          padding: 12,
          fontSize: 16,
          borderRadius: 6,
          border: '1px solid #ccc',
          resize: 'vertical',
          boxSizing: 'border-box',
        }}
        disabled={loading}
      />

      <div style={{ marginTop: 12, display: 'flex', gap: '12px' }}>
        <button
          onClick={handleAsk}
          disabled={loading || !question.trim()}
          style={{
            flex: 1,
            padding: 12,
            backgroundColor: loading ? '#95a5a6' : '#27ae60',
            color: '#fff',
            fontSize: 16,
            border: 'none',
            borderRadius: 6,
            cursor: loading ? 'not-allowed' : 'pointer',
            fontWeight: 'bold',
            transition: 'background-color 0.3s ease',
          }}
        >
          {loading ? 'Thinking...' : 'Ask'}
        </button>

        <button
          onClick={handleClear}
          disabled={loading && !question && !answer}
          style={{
            flex: 1,
            padding: 12,
            backgroundColor: '#e74c3c',
            color: '#fff',
            fontSize: 16,
            border: 'none',
            borderRadius: 6,
            cursor: 'pointer',
            fontWeight: 'bold',
            transition: 'background-color 0.3s ease',
          }}
        >
          Clear
        </button>
      </div>

      {answer && (
        <div
          style={{
            marginTop: 24,
            backgroundColor: '#fff',
            padding: 16,
            borderRadius: 6,
            border: '1px solid #ddd',
            position: 'relative',
            whiteSpace: 'pre-wrap',
            fontSize: 16,
            color: '#333',
            minHeight: 100,
          }}
        >
          <strong style={{ display: 'block', marginBottom: 8 }}>Answer:</strong>
          {answer}
          <button
            onClick={handleCopy}
            style={{
              position: 'absolute',
              top: 12,
              right: 12,
              backgroundColor: copied ? '#27ae60' : '#3498db',
              color: '#fff',
              border: 'none',
              borderRadius: 4,
              padding: '6px 10px',
              cursor: 'pointer',
              fontSize: 14,
              userSelect: 'none',
              transition: 'background-color 0.3s ease',
            }}
            title='Copy answer'
          >
            {copied ? 'Copied!' : 'Copy'}
          </button>
        </div>
      )}
    </div>
  );
}
