import React from 'react';
import { NavLink } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Collapse } from 'bootstrap'; // Import Collapse explicitly

const navItems = [
  { label: 'Home', to: '/' },
  { label: 'Login', to: '/login' },
  { label: 'Language', to: '/prog' },
  {
    label: 'AI',
    dropdown: [
      { label: 'Gemini', to: '/gemini' },
      { label: 'Wikipedia', to: '/wiki' },
      { label: 'Youtube', to: '/youtube' },
    ],
  },
];

function Navbar() {
  const handleNavLinkClick = () => {
    const navbarCollapse = document.getElementById('navbarNav');
    if (!navbarCollapse) return;

    if (navbarCollapse.classList.contains('show')) {
      setTimeout(() => {
        const collapseInstance = Collapse.getInstance(navbarCollapse);
        if (collapseInstance) {
          collapseInstance.hide();
        } else {
          navbarCollapse.classList.remove('show');
        }
      }, 2000);
    }
  };

  return (
    <>
      <nav className='navbar navbar-expand-lg navbar-dark bg-primary fixed-top'>
        <div className='container-fluid'>
          <NavLink className='navbar-brand' to='/'>
            Ai Model
          </NavLink>
          <button
            className='navbar-toggler'
            type='button'
            data-bs-toggle='collapse'
            data-bs-target='#navbarNav'
            aria-controls='navbarNav'
            aria-expanded='false'
            aria-label='Toggle navigation'
          >
            <span className='navbar-toggler-icon'></span>
          </button>

          <div
            className='collapse navbar-collapse justify-content-end'
            id='navbarNav'
          >
            <ul className='navbar-nav'>
              {navItems.map((item) =>
                !item.dropdown ? (
                  <li className='nav-item' key={item.label}>
                    <NavLink
                      to={item.to}
                      className={({ isActive }) =>
                        'nav-link' + (isActive ? ' active' : '')
                      }
                      onClick={handleNavLinkClick}
                    >
                      {item.label}
                    </NavLink>
                  </li>
                ) : (
                  <li className='nav-item dropdown' key={item.label}>
                    <a
                      href='#!'
                      className='nav-link dropdown-toggle'
                      role='button'
                      data-bs-toggle='dropdown'
                      aria-expanded='false'
                    >
                      {item.label}
                    </a>
                    <ul className='dropdown-menu dropdown-menu-end'>
                      {item.dropdown.map((sub) => (
                        <li key={sub.label}>
                          <NavLink
                            to={sub.to}
                            className='dropdown-item'
                            onClick={handleNavLinkClick}
                          >
                            {sub.label}
                          </NavLink>
                        </li>
                      ))}
                    </ul>
                  </li>
                )
              )}
            </ul>
          </div>
        </div>
      </nav>

      <style>{`
        body {
          padding-top: 56px;
          overflow-x: hidden;
        }
      `}</style>
    </>
  );
}

export default Navbar;
